import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  loanId: PropTypes.string.isRequired,
  loanAmount: PropTypes.number.isRequired,
  UEN: PropTypes.string,
  processingFee: PropTypes.number,
  tenure: PropTypes.number,
  RepaymentMethody: PropTypes.string,
  loanType: PropTypes.string,
});

export const formValues = PropTypes.shape({
  loanId: PropTypes.string,
  loanAmount: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  UEN: PropTypes.string,
  processingFee: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  tenure: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  RepaymentMethody: PropTypes.string,
  loanType: PropTypes.string,
});

export const formTouched = PropTypes.shape({
  loanId: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  loanAmount: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  UEN: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  processingFee: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  tenure: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  RepaymentMethody: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  loanType: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  loanId: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  loanAmount: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  UEN: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  processingFee: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  tenure: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  RepaymentMethody: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  loanType: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
