import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/loans';

export const apiLoanGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiLoanPost = async (serviceUrl, loan) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: loan ? JSON.stringify(loan) : null,
  };
  return request(url, options);
};

export const apiLoanPut = async (serviceUrl, loan) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: loan ? JSON.stringify(loan) : null,
  };
  return request(url, options);
};

export const apiLoanDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
