import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { formValues, formTouched, formErrors } from 'components/__types__/loan';
import { withFormik } from 'formik';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import { compose } from 'recompose';
import * as Yup from 'yup';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import ConfirmationDialogTrigger from 'components/common/ConfirmationDialogTrigger';

const styles = (theme) => ({
  root: {
    margin: theme.spacing(3),
  },
  textField: {
    width: '100%',
  },
});
class LoanForm extends PureComponent {
  constructor(props) {
    super(props);
    this.handleConfirmationDialogAction = this.handleConfirmationDialogAction.bind(this);
  }

  handleConfirmationDialogAction(action) {
    const { onDelete, values } = this.props;
    switch (action) {
      case ConfirmationDialogTrigger.CONFIRM: {
        onDelete(values);
        break;
      }
      default:
        break;
    }
  }

  render() {
    const {
      classes,
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit: formikHandleSubmit,
      onDelete,
      onCancelEditing,
      isSubmitting,
      t,
    } = this.props;

    const getHelperText = (field) => (errors[field] && touched[field] ? errors[field] : '');

    const handleSubmit = (e) => {
      e.stopPropagation(); // avoids double submission caused by react-shadow-dom-retarget-events
      formikHandleSubmit(e);
    };

    return (
      <form onSubmit={handleSubmit} className={classes.root} data-testid="loan-form">
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              id="loan-loanId"
              error={errors.loanId && touched.loanId}
              helperText={getHelperText('loanId')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.loanId}
              name="loanId"
              label={t('entities.loan.loanId')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="loan-loanAmount"
              error={errors.loanAmount && touched.loanAmount}
              helperText={getHelperText('loanAmount')}
              className={classes.textField}
              type="number"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.loanAmount}
              name="loanAmount"
              label={t('entities.loan.loanAmount')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="loan-UEN"
              error={errors.UEN && touched.UEN}
              helperText={getHelperText('UEN')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.UEN}
              name="UEN"
              label={t('entities.loan.UEN')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="loan-processingFee"
              error={errors.processingFee && touched.processingFee}
              helperText={getHelperText('processingFee')}
              className={classes.textField}
              type="number"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.processingFee}
              name="processingFee"
              label={t('entities.loan.processingFee')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="loan-tenure"
              error={errors.tenure && touched.tenure}
              helperText={getHelperText('tenure')}
              className={classes.textField}
              type="number"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.tenure}
              name="tenure"
              label={t('entities.loan.tenure')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="loan-RepaymentMethody"
              error={errors.RepaymentMethody && touched.RepaymentMethody}
              helperText={getHelperText('RepaymentMethody')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.RepaymentMethody}
              name="RepaymentMethody"
              label={t('entities.loan.RepaymentMethody')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="loan-loanType"
              error={errors.loanType && touched.loanType}
              helperText={getHelperText('loanType')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.loanType}
              name="loanType"
              label={t('entities.loan.loanType')}
            />
          </Grid>
          {onDelete && (
            <ConfirmationDialogTrigger
              onCloseDialog={this.handleConfirmationDialogAction}
              dialog={{
                title: t('entities.loan.deleteDialog.title'),
                description: t('entities.loan.deleteDialog.description'),
                confirmLabel: t('common.yes'),
                discardLabel: t('common.no'),
              }}
              Renderer={({ onClick }) => (
                <Button onClick={onClick} disabled={isSubmitting}>
                  {t('common.delete')}
                </Button>
              )}
            />
          )}

          <Button onClick={onCancelEditing} disabled={isSubmitting} data-testid="cancel-btn">
            {t('common.cancel')}
          </Button>

          <Button type="submit" color="primary" disabled={isSubmitting} data-testid="submit-btn">
            {t('common.save')}
          </Button>
        </Grid>
      </form>
    );
  }
}

LoanForm.propTypes = {
  classes: PropTypes.shape({
    root: PropTypes.string,
    textField: PropTypes.string,
    submitButton: PropTypes.string,
    button: PropTypes.string,
    downloadAnchor: PropTypes.string,
  }),
  values: formValues,
  touched: formTouched,
  errors: formErrors,
  handleChange: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  onDelete: PropTypes.func,
  onCancelEditing: PropTypes.func,
  isSubmitting: PropTypes.bool.isRequired,
  t: PropTypes.func.isRequired,
  i18n: PropTypes.shape({ language: PropTypes.string }).isRequired,
};

LoanForm.defaultProps = {
  onCancelEditing: () => {},
  classes: {},
  values: {},
  touched: {},
  errors: {},
  onDelete: null,
};

const emptyLoan = {
  loanId: '',
  loanAmount: '',
  UEN: '',
  processingFee: '',
  tenure: '',
  RepaymentMethody: '',
  loanType: '',
};

const validationSchema = Yup.object().shape({
  loanId: Yup.string().required(),
  loanAmount: Yup.number().required().min(200),
  UEN: Yup.string(),
  processingFee: Yup.number(),
  tenure: Yup.number(),
  RepaymentMethody: Yup.string(),
  loanType: Yup.string(),
});

const formikBag = {
  mapPropsToValues: ({ loan }) => loan || emptyLoan,

  enableReinitialize: true,

  validationSchema,

  handleSubmit: (values, { setSubmitting, props: { onSubmit } }) => {
    onSubmit(values);
    setSubmitting(false);
  },

  displayName: 'LoanForm',
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withTranslation(),
  withFormik(formikBag)
)(LoanForm);
